<template>
  <li :class="$style.item">
    <label :class="$style.checkLabel">
      <input :class="$style.checkbox" v-model="checked" type="checkbox" />
      {{ checked ? 'Выполнено' : 'Не выполнено' }}
    </label>

    <editable-text-component
      size="sm"
      :value="todo.text"
      :isInput="false"
      :completed="todo.completed"
      :content="todo.text.trim() ? todo.text : 'Нажмите, чтобы изменить текст'"
      @input="UPDATE_TODO({ ...todo, text: $event.target.value })"
    />

    <div :class="$style.button">
      <button-component size="small" @click="DELETE_TODO(todo.id)">
        Удалить
      </button-component>
    </div>
  </li>
</template>

<script lang="ts">
import { defineComponent, PropType } from 'vue'
import { mapMutations } from 'vuex'
import ToDo from '@/models/ToDoModel'
import ButtonComponent from './ButtonComponent.vue'
import EditableTextComponent from './EditableTextComponent.vue'

export default defineComponent({
  components: { ButtonComponent, EditableTextComponent },

  props: {
    todo: {
      type: Object as PropType<ToDo>,
      required: true,
    },
  },

  data() {
    return {
      editable: false,
      focused: false,
    }
  },

  methods: {
    ...mapMutations(['DELETE_TODO', 'UPDATE_TODO']),

    changeEditable() {
      this.editable = !this.editable
    },
  },

  computed: {
    checked: {
      get(): boolean {
        return this.todo.completed
      },
      set(value: boolean) {
        this.UPDATE_TODO({ ...this.todo, completed: value })
      },
    },
  },

  watch: {
    editable() {
      if (this.editable) {
        const input = this.$refs.input as HTMLInputElement
        this.$nextTick(() => input.focus())
      }
    },

    focused() {
      if (!this.focused && this.editable) {
        this.changeEditable()
      }
    },
  },
})
</script>

