  min-width: 100px;
  max-width: 300px;
  max-height: 300px;
  box-sizing: border-box;
  word-wrap: break-word;
  cursor: text;
}
.button {
  display: flex;
  align-items: center;
  justify-content: center;
}
.completed {
  text-decoration: line-through;
}
</style>
