<template>
  <div :class="$style.note">
    <div :class="$style.options">
      <options-component />
    </div>

    <div :class="$style.create">
      <button-component
        ref="addTodoButton"
        :disabled="disabled"
        @click="ADD_TODO"
        title="Добавить новую заметку"
      >
        <span class="material-icons"> add </span>
        <span>Добавить заметку</span>
      </button-component>

      <editable-text-component
        size="md"
        float="right"
        maxlength="30"
        :value="note.title"
        :isInput="true"
        :completed="false"
        :content="note.title ? note.title : 'Нажмите, что изменить название'"
        @input="UPDATE_TITLE($event.target.value)"
      />
    </div>

    <div :class="$style.todos">
      <ul :class="$style.ul">
        <todo-item-component
          v-for="todo in note.todos"
          :todo="todo"
          :key="todo.id"
        />
      </ul>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue'
import { mapActions, mapGetters, mapMutations, mapState } from 'vuex'
import ToDo from '@/models/ToDoModel'
import Note from '@/models/NoteModel'
import TodoItemComponent from '@/components/TodoItemComponent.vue'
import OptionsComponent from '@/components/OptionsComponent.vue'
import ButtonComponent from '@/components/ButtonComponent.vue'
import EditableTextComponent from '@/components/EditableTextComponent.vue'

export default defineComponent({
  name: 'Note',
  components: {
    TodoItemComponent,
    ButtonComponent,
    OptionsComponent,
    EditableTextComponent,
  },

  data() {
    return {
      disabled: false,
      editable: false,
      focused: false,
    }
  },

  methods: {
    ...mapMutations(['ADD_TODO', 'UPDATE_TITLE', 'SET_CURRENT_NOTE']),
    ...mapActions(['fetchCurrentNote']),

    changeEditable() {
      this.editable = !this.editable
    },

    fetchNote() {
      if (this.$router.currentRoute.value.params.id) {
        const routeId: string =
          this.$router.currentRoute.value.params.id.toString()
        this.fetchCurrentNote(routeId)
      } else {
        this.clearNote()
      }
    },

    clearNote() {
      this.SET_CURRENT_NOTE({
        title: '',
        todos: [] as ToDo[],
      } as Note)
    },
  },

  computed: {
    ...mapState({ note: 'currentNote' }),
    ...mapGetters(['getTodosLength']),
  },

  watch: {
    editable() {
      const input = this.$refs.input as HTMLInputElement
      if (this.editable) {
        this.$nextTick(() => input.focus())
      }
    },

    focused() {
      if (!this.focused && this.editable) {
        this.changeEditable()
      }
    },

    getTodosLength() {
      if (this.getTodosLength >= 5) {
        this.disabled = true
      } else {
        this.disabled = false
      }
    },
  },

  mounted() {
    this.fetchNote()
  },

  beforeRouteLeave(to, from, next) {
    this.clearNote()
    next()
  },
})
</script>

