
import { defineComponent } from 'vue'
import ButtonComponent from '@/components/ButtonComponent.vue'
import PopupComponent from '@/components/PopupComponent.vue'

export default defineComponent({
  components: { ButtonComponent, PopupComponent },

  methods: {
    saveNote() {
      this.$store.dispatch('saveNote')
      this.$router.push('/')
    },

    async deleteNote() {
      const popup = (await this.$refs.popup) as typeof PopupComponent
      const popupResult = await popup.open('Удалить записку?')

      if (popupResult) {
        this.$store.commit('DELETE_NOTE')
        this.$router.push('/')
      }
    },

    async cancelEdit() {
      const popup = (await this.$refs.popup) as typeof PopupComponent
      const popupResult = await popup.open('Выйти без сохранения?')

      if (popupResult) {
        this.$router.push('/')
      }
    },
  },
})
