
import { defineComponent } from 'vue'

// https://github.com/vuejs/vue-next/pull/3607
export default defineComponent({
  props: {
    size: {
      type: String,
      default: 'sm',
    },
    float: {
      type: String,
      default: 'center',
    },
    content: String,
    isInput: Boolean,
    completed: Boolean,
    value: String,
    maxlength: String,
  },

  data() {
    return {
      editable: false,
      focused: false,
    }
  },

  methods: {
    onFocus() {
      this.focused = true
    },

    onBlur() {
      this.focused = false
    },

    changeEditable() {
      this.editable = !this.editable
    },
  },

  watch: {
    editable() {
      if (this.editable) {
        const input = this.$refs.inputRef as HTMLInputElement
        this.$nextTick(() => input.focus())
      }
    },

    focused() {
      if (!this.focused && this.editable) {
        this.changeEditable()
      }
    },
  },
})
