
import { defineComponent } from 'vue'
import { mapActions, mapGetters, mapMutations, mapState } from 'vuex'
import ToDo from '@/models/ToDoModel'
import Note from '@/models/NoteModel'
import TodoItemComponent from '@/components/TodoItemComponent.vue'
import OptionsComponent from '@/components/OptionsComponent.vue'
import ButtonComponent from '@/components/ButtonComponent.vue'
import EditableTextComponent from '@/components/EditableTextComponent.vue'

export default defineComponent({
  name: 'Note',
  components: {
    TodoItemComponent,
    ButtonComponent,
    OptionsComponent,
    EditableTextComponent,
  },

  data() {
    return {
      disabled: false,
      editable: false,
      focused: false,
    }
  },

  methods: {
    ...mapMutations(['ADD_TODO', 'UPDATE_TITLE', 'SET_CURRENT_NOTE']),
    ...mapActions(['fetchCurrentNote']),

    changeEditable() {
      this.editable = !this.editable
    },

    fetchNote() {
      if (this.$router.currentRoute.value.params.id) {
        const routeId: string =
          this.$router.currentRoute.value.params.id.toString()
        this.fetchCurrentNote(routeId)
      } else {
        this.clearNote()
      }
    },

    clearNote() {
      this.SET_CURRENT_NOTE({
        title: '',
        todos: [] as ToDo[],
      } as Note)
    },
  },

  computed: {
    ...mapState({ note: 'currentNote' }),
    ...mapGetters(['getTodosLength']),
  },

  watch: {
    editable() {
      const input = this.$refs.input as HTMLInputElement
      if (this.editable) {
        this.$nextTick(() => input.focus())
      }
    },

    focused() {
      if (!this.focused && this.editable) {
        this.changeEditable()
      }
    },

    getTodosLength() {
      if (this.getTodosLength >= 5) {
        this.disabled = true
      } else {
        this.disabled = false
      }
    },
  },

  mounted() {
    this.fetchNote()
  },

  beforeRouteLeave(to, from, next) {
    this.clearNote()
    next()
  },
})
