import { createStore } from 'vuex'
import Note from '@/models/NoteModel'
import ToDo from '@/models/ToDoModel'

const NOTES = 'NOTES'
const setNotesToLocalStorage = (key: string, value: Note[]): void => {
  localStorage.setItem(key, JSON.stringify(value))
}
const getNotesFromLocalStorage = (key: string): Note[] => {
  return JSON.parse(localStorage.getItem(key) ?? '[]') as Note[]
}

export default createStore({
  state: {
    notes: getNotesFromLocalStorage(NOTES),
    currentNote: {
      title: '',
      todos: [] as ToDo[],
      id: '',
    } as Note,
  },

  mutations: {
    SET_CURRENT_NOTE(state, payload: Note) {
      state.currentNote = payload
    },

    ADD_NOTE(state) {
      state.notes.push({ ...state.currentNote, id: Date.now().toString() })
    },

    DELETE_NOTE(state) {
      state.notes = state.notes.filter(note => note.id != state.currentNote.id)
      setNotesToLocalStorage(NOTES, state.notes)
    },

    UPDATE_NOTE(state) {
      const note = state.notes.find(note => note.id === state.currentNote.id)
      const index = state.notes.indexOf(note as Note)
      state.notes[index] = state.currentNote
    },

    UPDATE_TITLE(state, payload: string) {
      state.currentNote.title = payload
    },

    UPDATE_TODO(state, payload: ToDo) {
      const todo = state.currentNote.todos.find(t => t.id === payload.id)
      const index = state.currentNote.todos.indexOf(todo as ToDo)
      state.currentNote.todos[index] = payload
    },

    ADD_TODO(state) {
      state.currentNote.todos.push({
        text: '',
        completed: false,
        id: Date.now().toString(),
      })
    },

    DELETE_TODO(state, todoId: string) {
      state.currentNote.todos = state.currentNote.todos.filter(
        todo => todo.id !== todoId
      )
    },
  },

  actions: {
    saveNote({ state, commit }) {
      const isOldNote: boolean = this.state.notes.some(
        el => el.id === this.state.currentNote.id
      )

      state.currentNote.todos.forEach((todo, idx) => {
        if (todo.text.trim() === '') {
          commit('UPDATE_TODO', { ...todo, text: `Новая заметка ${idx + 1}` })
        }
      })

      const isNoteTitleEmpty = state.currentNote.title.trim() === ''

      if (isOldNote) {
        if (isNoteTitleEmpty) {
          commit('UPDATE_TITLE', `Новая записка ${state.notes.length}`)
        }
        commit('UPDATE_NOTE')
      } else {
        if (isNoteTitleEmpty) {
          commit('UPDATE_TITLE', `Новая записка ${state.notes.length + 1}`)
        }
        commit('ADD_NOTE')
      }
      setNotesToLocalStorage(NOTES, state.notes)
    },

    fetchCurrentNote({ commit }, noteId: string) {
      const note = JSON.parse(
        JSON.stringify(this.state.notes.find(note => note.id === noteId))
      )
      commit('SET_CURRENT_NOTE', note)
    },

    updateCurrentNote({ commit }, note: Note) {
      commit('SET_CURRENT_NOTE', note)
    },
  },

  getters: {
    getTodosLength(state): number {
      return state.currentNote.todos.length
    },
  },

  strict: true,
})
