<template>
  <div :class="$style.list_wrap">
    <div :class="$style.search">
      <label :class="$style.search_label">
        <span :class="$style.search_title"> Поиск по названию </span>
        <input
          :class="$style.search_input"
          type="text"
          v-model="searchInput"
          placeholder="Введите название"
        />
      </label>
    </div>

    <ul v-if="!filteredNotes.length" :class="$style.list">
      <li :class="[$style.item, $style.empty]">Записок не найдено</li>
    </ul>

    <ul v-else :class="$style.list">
      <li :class="$style.item" v-for="note in filteredNotes" :key="note.id">
        <div :class="$style.id">{{ note.id }}</div>
        <div :class="$style.content">
          <div>
            <h2 :class="$style.title">{{ note.title }}</h2>
            <ul :class="$style.todo_list">
              <li
                :class="[
                  $style.todo_item,
                  { [$style.completed]: todo.completed },
                ]"
                v-for="(todo, index) in note.todos"
                :key="index"
              >
                {{ todo.text }}
              </li>
            </ul>
          </div>
        </div>
        <div :class="$style.item_button">
          <button-component @click="$router.push(`/note/${note.id}`)">
            <span class="material-icons"> edit </span>
            <span>Изменить</span>
          </button-component>
        </div>
      </li>
    </ul>
  </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue'
import { mapState } from 'vuex'
import ButtonComponent from '@/components/ButtonComponent.vue'
import Note from '@/models/NoteModel'

export default defineComponent({
  name: 'List',

  components: { ButtonComponent },

  data() {
    return {
      searchInput: '',
    }
  },

  computed: {
    ...mapState(['notes']),

    filteredNotes(): Note[] {
      return this.notes.filter((note: Note) =>
        note.title.toLowerCase().includes(this.searchInput.toLowerCase())
      )
    },
  },
})
</script>

