<template>
  <div :class="[$style.textWrap, $style[size], $style[float]]">
    <span
      v-show="!editable"
      tabindex="0"
      :class="[$style.text, { [$style.completed]: completed }]"
      @click="changeEditable"
      @keypress.enter.space="changeEditable"
    >
      {{ content }}
    </span>
    <input
      v-if="isInput"
      v-show="editable"
      ref="inputRef"
      type="text"
      :class="$style.input"
      :maxlength="maxlength"
      :value="value"
      @input="$emit('input', $event)"
      @focus="onFocus"
      @blur="onBlur"
      @keypress.enter="changeEditable"
    />
    <textarea
      v-else
      v-show="editable"
      ref="inputRef"
      type="text"
      :class="[$style.input, $style.textarea]"
      :maxlength="maxlength"
      :value="value"
      @input="$emit('input', $event)"
      @focus="onFocus"
      @blur="onBlur"
      @keypress.enter="changeEditable"
    />
  </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue'

// https://github.com/vuejs/vue-next/pull/3607
export default defineComponent({
  props: {
    size: {
      type: String,
      default: 'sm',
    },
    float: {
      type: String,
      default: 'center',
    },
    content: String,
    isInput: Boolean,
    completed: Boolean,
    value: String,
    maxlength: String,
  },

  data() {
    return {
      editable: false,
      focused: false,
    }
  },

  methods: {
    onFocus() {
      this.focused = true
    },

    onBlur() {
      this.focused = false
    },

    changeEditable() {
      this.editable = !this.editable
    },
  },

  watch: {
    editable() {
      if (this.editable) {
        const input = this.$refs.inputRef as HTMLInputElement
        this.$nextTick(() => input.focus())
      }
    },

    focused() {
      if (!this.focused && this.editable) {
        this.changeEditable()
      }
    },
  },
})
</script>

<style lang="scss" module>
.textWrap {
  display: flex;
  align-items: center;
  font-size: 0.8rem;
}
.left {
  justify-content: flex-start;
}
.center {
  justify-content: center;
}
.right {
  justify-content: flex-end;
}
.sm {
  font-size: 0.8rem;
}
.md {
  font-size: 1rem;
}
.input,
.text {
  width: 100%;
  height: 1.2rem;
  min-width: 100px;
  max-width: 300px;
  max-height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  box-sizing: border-box;
  word-wrap: break-word;
  cursor: text;
  outline: none;
  border: none;
  background-color: transparent;
  color: white;
  box-shadow: inset 0 2px 4px rgba(0, 0, 0, 0.6);
}
.input {
  border-bottom: 2px solid $color-link;
}
.md > .input,
.md > .text {
  height: 100%;
}
.completed {
  text-decoration: line-through;
}
</style>
